<template>
    <div class="main">
        <div ref="page" class="page" :style="{backgroundImage:'url('+ this.page.bg_image +')'}">
            <!-- <myMusic class="my-music" :my-music="music"></myMusic> -->
            <img src="static/page1/logo.png" alt="" class="title">
            <div class="myTable">
                
                <div class="titles"><b></b></div>
                <div class="items">
                    <label class="lable" for="name"></label>
                    <input value="" type="text" class="inputs" v-model.trim.lazy="buyer_name" ref="buyer_name" @blur="myChange"></input>
                </div>
                <div class="items">
                    <label class="lable" for="name"></label>
                    <input value="" type="tel" class="inputs" v-model.trim.number.lazy="buyer_phone" ref="buyer_phone" @blur="checkPhone"></input>
                </div>
                <div class="items">
                    <label class="lable" for="name"></label>
                    <input value="" type="text" class="inputs" v-model.trim.lazy="buyer_get_info" ref="buyer_get_info" @blur="checkMail"></input>
                </div>
                <div class="items">
                    <label class="lable" for="name"></label>
                    <input value="" type="text" class="inputs" v-model.trim.lazy="buyer_addr" ref="buyer_addr" @blur="myChange"></input>
                </div>
                <div class="des">
                    *
                </div>
                <img src="static/page3/button.png" @click="submitInfo" class="button">
            </div>
            <img src="static/page2/guan.png" alt="" class="bottom">
        </div>
        <alertSystem :content="alertInfo" v-show="hasShowSystemAlert" @rulesStatus="rulesStatus"></alertSystem>
    </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import Loading from '../../components/Loading';
    //
    import alertSystem from '../../components/alertSystem';
    export default {
        components: {
            Loading,
            alertSystem
        },
        data() {
            return {
                bounce:true,
                storeCode:"",
                //--------------- end-------------
                config: {},
                loading: false,
                inLoading: true, //
                images: {}, //--
                iCur: 0, //
                music: {},
                rulesId: 0,
                hasShowRules: false, //
                canGetPrize: false,  //
                hasShowPrizeResult: false, //
                inDraw: false, //
                rotationAngle: 0,
                hasGetPrize: false, // 
                prizeDrawNumberText: '', //
                prizeDrawNumber: 5, //
                prize: -1, //
                coupon_code: "", //ID
                prizeInfo: {},
                allPrize: {},  //                // --------------------------------------------
                showArr: [210, 270, 330, 390, 450, 510], //,
                imagesArr: [], //icon
                prizeImg:{},
                has_show_rules:true,
                hasShowSystemAlert: false,
                alertInfo:"",
                //-------------------------------------------
                buyer_name: "",         //
                buyer_phone: "",        //
                buyer_get_info: "",     //
                buyer_addr: ""          //
            }
        },
        computed: {
            ...mapState({
                imageArr: state => state.images,
                page: state => state.pages[1],
                activityId: state => state.activityId,
                sessionKey: state => state.sessionKey,
                couponBagHost: state => state.couponBagHost
            })
        },
        beforeCreate: function () {
        },
        created() {
            let ajax_url = window.location.href.split('#')[0];
            let url = "https://ab.aikaka.com.cn/PublicService/Weixin/getJsSdkConfig.php", _self = this
            let params = {
                "url": url,
                "type": "GET",
                "dataType": "jsonp",
                "data": {
                    "_t": Math.random(),
                    "url": ajax_url
                },
                "success": function (res) {
                    _self.$wx.config({
                        debug: false,
                        appId: res.appId,
                        timestamp: res.timestamp,
                        nonceStr: res.nonceStr,
                        signature: res.signature,
                        jsApiList: [
                            'onMenuShareTimeline',  // 
                            'onMenuShareAppMessage',    // 
                            'onMenuShareQQ',    // QQ
                            'onMenuShareWeibo', // 
                            'onMenuShareQZone', // QQ
                            'chooseImage', // 
                            'previewImage', // 
                            'uploadImage',  // 
                            'downloadImage', // 
                            'getNetworkType', // 
                            'openLocation', // 
                            'getLocation', // 
                            'startSearchBeacons', // ibeacon
                            'stopSearchBeacons', // ibeacon
                            'onSearchBeacons', // ibeacon
                            'hideOptionMenu', // 
                            'closeWindow', // 
                            'hideMenuItems', // 
                            'hideAllNonBaseMenuItem', //  
                            'showAllNonBaseMenuItem', // 
                            'scanQRCode', // 
                            'addCard' // 
                        ]
                    });
                    _self.$wx.ready(function () {
                        //
                        _self.$wx.hideOptionMenu()
                    })
                }
            }
            let test=this.$ajax(params)
            this.checkedRouter()
        },
        watch: {
            '$route': 'checkedRouter'
        },
        mounted() {
            document.getElementsByTagName("body")[0].style.height = document.body.clientHeight
            document.getElementsByTagName("html")[0].style.height = document.body.clientHeight
        },
        updated() {
        },
        methods: {
            checkedRouter() {
                // 
                this.openId = this.$route.query.openid ? this.$route.query.openid : this.urlGet()['openid'] ? this.urlGet()['openid'].replace(/#\//, "") : ""
                this.storeCode = this.$route.query.store_code ? this.$route.query.store_code : this.storeCode
                this.coupon_code = this.coupon_code ? this.coupon_code: this.urlGet()['coupon_code'] ? this.urlGet()['coupon_code'].replace(/#\//, "") : localStorage[this.sessionKey]
                //  xjjOpenId  start  
                if (this.openId !== "" && typeof this.openId !== 'undefined' && this.openId !== null) {
                    this.start()
                } else {
                    this.authorize()
                }
            },
            //openid
            urlGet() {
                let aQuery = window.location.href.split("?") //Get
                let aGET = new Array()
                if (aQuery.length > 1) {
                    let aBuf = aQuery[1].split("&");
                    for (let i = 0, iLoop = aBuf.length; i < iLoop; i++) {
                        let aTmp = aBuf[i].split("=") //keyValue
                        aGET[aTmp[0]] = decodeURIComponent(aTmp[1])
                    }
                }
                return aGET
            },
            authorize() {
                let url = window.location.href.split('?')[0]
                let host = url + '?activity_id=' + this.activityId + '&store_code=' + this.storeCode
                window.location.href = "http://ab.aikaka.com.cn/PublicService/Weixin/oauth/option?callback_url=" + host
            },
            start() { 
                this.allPrize = this.page.prize_img_box
                this.$nextTick(() => {
                    this.$refs.page.style.height = document.body.clientHeight + 'px'
                })
            },
            //
            submitInfo() {
                if (!this.buyer_name) {
                    this.$refs.buyer_name.className = "error"
                    return
                }
                if (this.buyer_phone.toString().length != 11) {
                    this.$refs.buyer_phone.className = "error"
                    return
                }
                if (this.buyer_get_info=="" || this.buyer_get_info.length >50) {
                    this.$refs.buyer_get_info.className = "error"
                    return
                }
                // let mail = this.buyer_get_info
                // let reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
                // if (!reg.test(mail)) {
                //     this.$refs.buyer_get_info.className = "error"
                //     return
                // }

                if (!this.buyer_addr) {
                    this.$refs.buyer_addr.className = "error"
                    return
                }
                let coupon_code = this.coupon_code
                let url = "/cocacola/Acmp/AcmpApi/prize/updateUserInfo", sub_data = {}
                sub_data.buyer_name = this.buyer_name
                sub_data.buyer_phone = this.buyer_phone
                sub_data.buyer_get_info = this.buyer_get_info
                sub_data.buyer_addr = this.buyer_addr
                sub_data.coupon_code = coupon_code
                this.$axios({
                    method: 'post',
                    url: url,
                    data: sub_data
                }).then(result => {
                    if (result.data.return_code == 200) {
                        //
                        let urls = "/cocacola/Acmp/AcmpApi/consume/scrape"
                        this.$axios({
                            method: 'post',
                            url: urls,
                            data: sub_data
                        }).then(result => {
                            //$("#loadingToast").hide();
                            if (result.data.return_code == 200) {
                                this.$router.replace({ path: '/Last', query: {} })
                                return
                            } else {
                                this.alertInfo = result.data.return_msg
                                this.hasShowSystemAlert = true
                            }
                        })
                    } else {
                        this.alertInfo = result.data.return_msg
                        this.hasShowSystemAlert = true
                    }
                })
            },
            //
            getPrize() {
                
            },
            //
            rulesStatus(){
                this.hasShowSystemAlert = false
            },
            myChange(e) {
                if (e.target.value) {
                    e.target.classList = ["inputs"]
                } else {
                    e.target.classList = ["error"]
                }
            },
            //
            checkMail(e) {
                let mail = e.target.value
                // let reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
                // if (reg.test(mail)){
                //     e.target.classList = ["inputs"]
                // } else{
                //     e.target.classList = ["error"]
                // }
                if (mail.length == 0 || mail.length > 50 ) {
                    e.target.classList = ["error"]
                } else {
                    e.target.classList = ["inputs"]
                }
            },
            //
            checkPhone(e) {
                let phone = e.target.value
                if (phone.toString().length != 11) {
                    e.target.classList = ["error"]
                }else{
                    e.target.classList = ["inputs"]
                }
            }
        }
    }
</script>

<style scoped lang="less">
    @import "Index";
</style>


