<template>
    <div class="main">
        <div ref="page" class="page" :style="{backgroundImage:'url('+ this.page.bg_image +')'}">
            <img src="static/page1/logo.png" alt="" class="title">
            <div class="myTable">
                <div class="titles"><b><i>OH!</i></b></div>
                <div class="items">
                    <b></b>
                </div>
                <img src="static/page3/return.png" class="return" @click="getPrize">
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    export default {
        components: {
        },
        data() {
            return {
                bounce: true,
                storeCode: "",
                //--------------- end-------------
                config: {},
                loading: false,
                inLoading: true, //
                images: {}, //--
                iCur: 0, //
                music: {},
                rulesId: 0,
                hasShowRules: false, //
                canGetPrize: false,  //
                hasShowPrizeResult: false, //
                inDraw: false, //
                rotationAngle: 0,
                hasGetPrize: false, // 
                prizeDrawNumberText: '', //
                prizeDrawNumber: 5, //
                prize: -1, //
                coupon_code: "", //ID
                prizeInfo: {},
                allPrize: {},  //                // --------------------------------------------
                showArr: [210, 270, 330, 390, 450, 510], //,
                imagesArr: [], //icon
                prizeImg: {},
                has_show_rules: true
            }
        },
        computed: {
            ...mapState({
                imageArr: state => state.images,
                page: state => state.pages[1],
                activityId: state => state.activityId
            }),
        },
        beforeCreate: function () {
        },
        created() {
            let ajax_url = window.location.href.split('#')[0];
            let url = "https://ab.aikaka.com.cn/PublicService/Weixin/getJsSdkConfig.php", _self = this
            let params = {
                "url": url,
                "type": "GET",
                "dataType": "jsonp",
                "data": {
                    "_t": Math.random(),
                    "url": ajax_url
                },
                "success": function (res) {
                    _self.$wx.config({
                        debug: false,
                        appId: res.appId,
                        timestamp: res.timestamp,
                        nonceStr: res.nonceStr,
                        signature: res.signature,
                        jsApiList: [
                            'onMenuShareTimeline',  // 
                            'onMenuShareAppMessage',    // 
                            'onMenuShareQQ',    // QQ
                            'onMenuShareWeibo', // 
                            'onMenuShareQZone', // QQ
                            'chooseImage', // 
                            'previewImage', // 
                            'uploadImage',  // 
                            'downloadImage', // 
                            'getNetworkType', // 
                            'openLocation', // 
                            'getLocation', // 
                            'startSearchBeacons', // ibeacon
                            'stopSearchBeacons', // ibeacon
                            'onSearchBeacons', // ibeacon
                            'hideOptionMenu', // 
                            'closeWindow', // 
                            'hideMenuItems', // 
                            'hideAllNonBaseMenuItem', //  
                            'showAllNonBaseMenuItem', // 
                            'scanQRCode', // 
                            'addCard' // 
                        ]
                    });
                    _self.$wx.ready(function () {
                        //
                        _self.$wx.hideOptionMenu()
                    })
                }
            }
            let test = this.$ajax(params)
            this.checkedRouter()
        },
        watch: {
            '$route': 'checkedRouter'
        },
        mounted() {
        },
        updated() {
        },
        methods: {
            checkedRouter() {
                // 
                this.openId = this.$route.query.openid ? this.$route.query.openid : this.urlGet()['openid'] ? this.urlGet()['openid'].replace(/#\//, "") : ""
                this.storeCode = this.$route.query.store_code ? this.$route.query.store_code : this.storeCode
            },
            //openid
            urlGet() {
                let aQuery = window.location.href.split("?") //Get
                let aGET = new Array()
                if (aQuery.length > 1) {
                    let aBuf = aQuery[1].split("&");
                    for (let i = 0, iLoop = aBuf.length; i < iLoop; i++) {
                        let aTmp = aBuf[i].split("=") //keyValue
                        aGET[aTmp[0]] = decodeURIComponent(aTmp[1])
                    }
                }
                return aGET
            },
            authorize() {
                let url = window.location.href.split('?')[0]
                let host = url + '?activity_id=' + this.activityId + '&store_code=' + this.storeCode
                window.location.href = "https://ab.aikaka.com.cn/PublicService/Weixin/oauth/option?callback_url=" + host
            },
            //
            getPrize() {
                this.$router.replace({ path: '/', query: { openid: this.openId, activity_id: this.activityId, store_code: this.storeCode } })
            }
        }
    }
</script>

<style scoped lang="less">
    @import "Index";
</style>


